@import url("https://fonts.googleapis.com/css2?family=Noto+Sans+JP&family=Noto+Sans:wght@400;700&display=swap");
@import "./_global_theme";

$color-main: #e43f5a;
$nav-bg: #334365;
$color-header: #3958a7;
$color-nav-link: rgb(235, 235, 235);
$color-nav-link-active: $color-main;

body {
	margin: 0;
	font-family: "Noto Sans", "Noto Sans JP", -apple-system, BlinkMacSystemFont, "Segoe UI",
		"Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
		sans-serif;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
	font-size: 11pt;
	color: rgb(97, 97, 97);
	h1 {
		color: $color-header;
	}
	h2 {
		color: $primary-txt-dark;
	}
}

code {
	font-family: "Source Code Pro", Menlo, Monaco, Consolas, "Courier New", monospace;
	font-size: 12pt;
}
